.info__content {
  display: flex;
  flex-direction: column;
  gap: 20px;
  padding: 30px;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .info__content {
    padding: 20px 30px;
  }
}

@media screen and (max-width: 768px) {
  .info__content {
    padding: 10px 0;
  }
}
