.timeblock__image {
  font-size: 1.75rem;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  padding: 14px;
  /*
  background: #333;
  
  background-color: #2d2e32;*/
  background-color: #3e3e42;
  color: #f2f2f2;
  border-radius: 50%;
}

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .timeblock__image {
    padding: 12px;
  }
}

@media screen and (max-width: 768px) {
  .timeblock__image {
    padding: 10px;
    left: 0%;
    font-size: var(--font-size-h3);
  }
}
